body{
  margin: 0;
  padding: 0;
}

#gridContainer{
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

#grid{
  /* background-color: blue; */
  display: flex;
  flex-direction: column;
  gap: 0;
  border: 2px solid black;
  z-index: 99;
}

.row{
  display: flex;
}

.cellContainer{
  display: flex;
}

.cell{
  width: 50px;
  height: 50px;
  z-index: 1;
  background-color: white;
}

.cell.coloured{
  background-color: rgba(255, 0, 130, 1);
}

.cell.colouredPicked{
  background-color: rgba(0, 255, 162, 1);
}

 .cell:hover{
  background-color: rgba(0, 255, 162, 0.5);
} 

.rowVSep{
  display: flex;
  gap: 0;
}

.segmentH{
  position: 'absolute';
  width: 1px;
  height: 50px;
  background-color: black;
  margin-left: 1px;
  margin-right: 1px;
}

.segmentH.bold{
  width: 3px;
  margin-left: 0;
  margin-right: 0;
}
.segmentV{
  position: 'absolute';
  background-color: black;
  width: 52px;
  height: 1px;
  margin-top: 1px;
  margin-bottom: 1px;
}

.segmentV.bold{
  height: 3px;
  margin-top: 0;
  margin-bottom: 0;
}
.areaPicker{
  margin-left: 30px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
}

.areaPickerButton{
  width: 50px;
  height: 50px;
  margin: 5px;
}